function redirectIfNotLogin()
{
    if (!localStorage.getItem('token')) {
        location.href = LOGIN_URL;
    }
}

function callApi(method, url, data, callback)
{
    $.ajax({
        'type': method,
        'url': url,
        'data': data
    }).done(function(data) {
        callback(data);
    }).fail(function(data) {
        alert(data.responseJSON.response.errors.join(','));
    });
}